import React, { Component } from 'react';
import PORTFOLIO_DATA from './Data.js';
import TechnoCards from './TechnoCards';
import { PreviewContainer } from './portfolio.styles';
import  { Breakpoint , BreakpointProvider } from 'react-socks';
import SwipeableViews from 'react-swipeable-views';
import Blob from './images/decorations/blob1';
import Fade from 'react-reveal/Fade';


class TechnoPortfolio extends Component {
constructor(props){
    super(props);

    this.state = {
       projects : PORTFOLIO_DATA

    }
}

    render() {
        const {projects} = this.state;
        const styles = {
            root: {
              padding: '0 2rem',
              marginLeft : '-0.9rem'
            },
            slideContainer: {
              marginLeft:'-0.2rem',
              
            }, 
          
          };
          
        return (

     <BreakpointProvider>  

     <Fade bottom>
     <div className='text-teal-400 text-bold text-center text-6xl font-mono'> Projects 
     </div> </Fade> <br/>   
     
         <Breakpoint medium down>
            <SwipeableViews style={styles.root} slideStyle={styles.slideContainer}>
             {projects.map(({id, title, description})=> (
                          <TechnoCards key={id} title={title} description={description}/>
                        ))}
            </SwipeableViews>
            </Breakpoint>

       <Breakpoint large up>
            <PreviewContainer>
                {projects.map(({id, title, description})=> (
                          <TechnoCards key={id} title={title} description={description}/>
                ))}
            </PreviewContainer>
        </Breakpoint>

        <div className='-ml-32 md:-ml-48 md:w-1/4 w-1/2 md:-mt-24 -mt-12'>
        <Blob/>
        </div>      
    </BreakpointProvider>

        )
    }
}


export default TechnoPortfolio;